export const updateAddt=(state,title)=>{
    state.addt=title;
}
export const updateAddc=(state,content)=>{
    state.addc=content;
}
export const updateAddp=(state,type)=>{
    state.addp=type;
}
export const updateAddi=(state,img)=>{
    state.addi=img;
}



